<template>
  <header class="header">
    <nav class="nav">
      <div class="hamburger-menu">
        <input id="menu__toggle" v-model="menuToggle" type="checkbox" />
        <label
          class="menu__btn"
          :class="menuToggle ? 'menu--active' : ''"
          for="menu__toggle"
        >
          <span></span>
        </label>
        <ul class="menu__box">
          <li>
            <router-link to="/rolls" class="menu__item">Роллы</router-link>
          </li>
          <li><router-link to="/sets" class="menu__item">Сеты</router-link></li>
          <li>
            <router-link to="/actions" class="menu__item">Акции</router-link>
          </li>
          <li>
            <router-link to="/contacts" class="menu__item"
              >Контакты</router-link
            >
          </li>
        </ul>
      </div>
      <div class="logo">
        <router-link to="/"
          ><img src="../assets/logo.svg" alt=""
        /></router-link>
      </div>
      <div class="basket" :class="countItems > 0 ? 'basket--on' : ''">
        <router-link to="/cart">
          <img src="../assets/cart.svg" alt="" />
          <span>{{ props.countItems }}</span>
        </router-link>
      </div>
      <div class="menu">
        <router-link to="/rolls">Роллы</router-link>
        <router-link to="/sets">Сеты</router-link>
        <router-link to="/actions">Акции</router-link>
        <router-link to="/contacts">Контакты</router-link>
      </div>
      <div class="contact">
        <h3>+38(099)-456-78-90</h3>
        <p>ул. 23 Августа, 43</p>
      </div>
    </nav>
  </header>
</template>

<script setup>
import { ref } from "vue";

const props = defineProps({
  countItems: {
    type: Number,
    default: 0
  }
})

const menuToggle = ref(false);
</script>

<style lang="scss">
.header {
  width: 100%;
  max-height: 80px;
  margin: 0;

  .logo {
    width: 145px;
    @media (max-width: 420px) {
      width: 100px;
    }
  }
  .nav {
    color: black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
    transition: top 1.1s;

    .basket {
      display: block;
      position: absolute;
      right: 220px;
      @media (max-width: 820px) {
        position: relative;
        right: 0;
      }

      span {
        display: none;
      }
      &.basket--on {
        span {
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 12px;
          width: 20px;
          height: 20px;
          line-height: 0;
          background-color: red;
          color: white;
          border-radius: 50%;
          position: absolute;
          top: -5px;
          right: -7px;
        }
      }
      img {
        width: 30px;
      }
    }
    .contact {
      @media (max-width: 820px) {
        display: none;
      }

      h3 {
        margin: 0;
      }
      p {
        margin: 1rem 0 0;
      }
    }
  }
  .menu {
    font-size: 16px;
    font-weight: 700;
    width: 330px;
    padding: 0;
    @media (max-width: 820px) {
      display: none;
    }

    a {
      color: black;
      margin-right: 25px;
      transition: 0.2s linear;

      &:hover {
        color: #ff6047;
      }
    }
  }
  /* скрываем чекбокс */
  #menu__toggle {
    opacity: 0;
    display: none;
  }
  .hamburger-menu {
    display: none;

    @media (max-width: 820px) {
      display: block;
    }
  }
  #menu__toggle:checked ~ .menu__btn > span {
    transform: rotate(50deg);
  }
  #menu__toggle:checked ~ .menu__btn > span::before {
    top: 0;
    transform: rotate(0);
  }
  #menu__toggle:checked ~ .menu__btn > span::after {
    top: -4px;
    transform: rotate(80deg);
  }
  #menu__toggle:checked ~ .menu__box {
    visibility: visible;
    left: 0;
  }
  /* стилизуем кнопку */
  .menu__btn {
    display: flex; /* используем flex для центрирования содержимого */
    align-items: center; /* центрируем содержимое кнопки */
    position: relative;
    width: 35px;
    height: 26px;
    cursor: pointer;
    z-index: 100;

    &.menu--active {
      & > span,
      & > span::before,
      & > span::after {
        background-color: #fff !important;
      }
    }
  }
  /* добавляем "гамбургер" */
  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    display: block;
    position: relative;
    width: 40px; //100%;
    height: 5px;
    background-color: #000;
  }
  .menu__btn > span::before {
    content: "";
    top: -13px;
  }
  .menu__btn > span::after {
    content: "";
    top: 8px;
  }
  /* контейнер меню */
  .menu__box {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: fixed;
    visibility: hidden;
    top: 0;
    left: -100%;
    width: 70%;
    height: 220px;
    margin: 0;
    padding: 80px 0;
    list-style: none;
    text-align: center;
    background-color: rgb(54, 54, 54);
    box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2);

    @media (max-width: 420px) {
      width: 100%;
      height: 100vh;
      justify-content: space-evenly;
      padding: 0;
      z-index: 10;
    }
  }
  /* элементы меню */
  .menu__item {
    display: block;
    padding: 12px 24px;
    color: rgb(255, 255, 255);
    font-family: "Roboto", sans-serif;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
  }
  .menu__item:hover {
    background-color: rgb(17, 113, 155);
  }
  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    transition-duration: 0.25s;
  }
  .menu__box {
    transition-duration: 0.25s;
  }
  .menu__item {
    transition-duration: 0.25s;
  }
}
</style>
