<template>
  <div class="wrapper">
    <Header :countItems="countItems"></Header>
    <router-view @cartCount="updateCartCount"></router-view>
    <FooterVue></FooterVue>
  </div>
</template>

<script setup>
import Header from "./components/Header.vue";
import FooterVue from "./components/Footer.vue";
import { ref, onMounted } from "vue";

const countItems = ref(0);

const updateCartCount = (count) => countItems.value = count;

onMounted(() => {
  const params = localStorage.getItem("cartItems");
  if(params != 'undefined' && params != 'null' && params) {
    updateCartCount(JSON.parse(params).length);
  }
})

</script>

