<template>
  <div class="table">
    <div class="sets" v-for="(item, index) of items" :key="index">
      <img :src="getImgUrl(item.src)" alt="" />
      <h2>{{ item.title }}</h2>
      <p>{{ item.description }}</p>
      <p>
        <strong>{{ item.price }}</strong> грн
      </p>
      <button class="btn" :disabled="item.selected" :class="item.selected ?'disabled' : ''" @click="addToBasket(item)">
        {{ item.selected ? 'Добавлено' : 'Добавить в корзину' }}
      </button>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';

const props = defineProps({
  items: {
    type: Array,
    required: true,
  },
});

const emit= defineEmits(["cartCount"]);

let cartItems = [];
const listItems = ref(props.items);

const addToBasket = (item) => {
  item.selected = true;
  cartItems.push(item);
  emit('cartCount', cartItems.length);
  localStorage.setItem("cartItems", JSON.stringify(cartItems));
};

const getImgUrl = (src) => {
  return require(`../assets/${src}`);
};

onMounted(() => {
  const params = localStorage.getItem("cartItems");
  if(params != 'undefined' && params != 'null' && params) {
    cartItems = JSON.parse(params);
    cartItems.forEach(item => {
      listItems.value.forEach(i => {
        if(i.id === item.id && item.selected) {
          i.selected = true;
        }
      });
    });
  }
})

</script>

<style lang="scss">
.table {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 80px 25px;
  align-items: center;
  justify-content: center;
  margin: 0 auto 100px;
  padding: 0 10px;

  @media (max-width: 820px) {
    grid-template-columns: repeat(2, 1fr);
    gap: 50px 25px;
  }
  @media (max-width: 500px) {
    grid-template-columns: repeat(1, 1fr);
    gap: 100px 0;
  }

  .sets {
    height: 100%;
    margin: 0 auto;
    text-align: center;
    display: grid;
    grid-template-rows: 1fr;
    gap: 25px 0;

    img {
      margin: 0 auto;
      width: 95%;
      height: auto;
    }
    h2 {
      font-size: 2rem;
    }
    strong {
      font-style: italic;
      font-size: 25px;
    }
  }
}
</style>
